import { useState } from "react";
import { BsFillShieldLockFill, BsTelephoneFill } from "react-icons/bs";
import { CgSpinner } from "react-icons/cg";
import OtpInput from "otp-input-react";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import { initializeApp } from "firebase/app";
import { getAuth, signInWithPhoneNumber } from "firebase/auth"; // Removed RecaptchaVerifier import
import { toast, Toaster } from "react-hot-toast";

// Firebase config
const firebaseConfig = {
  apiKey: "AIzaSyA3gchspwUDrJGzrTDxoZIaTRo5d51EVRo",
  authDomain: "otp-auth-6103a.firebaseapp.com",
  projectId: "otp-auth-6103a",
  storageBucket: "otp-auth-6103a.firebasestorage.app",
  messagingSenderId: "244668945133",
  appId: "1:244668945133:web:45fa56a63bc12f715b32e4",
  measurementId: "G-SF8JQBZ15N"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const auth = getAuth(app);  // Initialize Firebase Auth

const App = () => {
  const [otp, setOtp] = useState(""); // State for OTP
  const [ph, setPh] = useState(""); // State for phone number
  const [loading, setLoading] = useState(false); // State for loading spinner
  const [showOTP, setShowOTP] = useState(false); // Toggle between phone and OTP input
  const [user, setUser] = useState(null); // User state after successful login

  // Function to send OTP to the phone number
  function onSignup() {
    setLoading(true); // Start loading spinner

    // Make sure phone number is in the correct format
    const formatPh = "+" + ph; // Ensure phone number is formatted with '+'

    signInWithPhoneNumber(auth, formatPh) // Send OTP without reCAPTCHA
      .then((confirmationResult) => {
        window.confirmationResult = confirmationResult; // Store confirmationResult for OTP verification
        setLoading(false);
        setShowOTP(true); // Show OTP input field
        toast.success("OTP sent successfully!");
      })
      .catch((error) => {
        setLoading(false);
        console.error(error);
        toast.error("Failed to send OTP.");
      });
  }

  // Function to verify the OTP entered by the user
  function onOTPVerify() {
    setLoading(true); // Start loading spinner
    window.confirmationResult
      .confirm(otp) // Confirm OTP
      .then((res) => {
        setUser(res.user); // Set user if successful
        setLoading(false);
      })
      .catch((err) => {
        setLoading(false);
        console.error(err);
        toast.error("OTP verification failed.");
      });
  }

  return (
    <section className="bg-emerald-500 flex items-center justify-center h-screen">
      <div>
        <Toaster toastOptions={{ duration: 4000 }} />
        {user ? (
          <h2 className="text-center text-white font-medium text-2xl">
             Login Success
          </h2>
        ) : (
          <div className="w-80 flex flex-col gap-4 rounded-lg p-4">
            <h1 className="text-center leading-normal text-white font-medium text-3xl mb-6">
              Welcome to <br /> CODE A PROGRAM
            </h1>
            {showOTP ? (
              <>
                <div className="bg-white text-emerald-500 w-fit mx-auto p-4 rounded-full">
                  <BsFillShieldLockFill size={30} />
                </div>
                <label htmlFor="otp" className="font-bold text-xl text-white text-center">
                  Enter your OTP
                </label>
                <OtpInput
                  value={otp}
                  onChange={setOtp} // Update OTP state
                  OTPLength={6}
                  otpType="number"
                  disabled={false}
                  autoFocus
                  className="opt-container"
                />
                <button
                  onClick={onOTPVerify}
                  className="bg-emerald-600 w-full flex gap-1 items-center justify-center py-2.5 text-white rounded"
                >
                  {loading && (
                    <CgSpinner size={20} className="mt-1 animate-spin" />
                  )}
                  <span>Verify OTP</span>
                </button>
              </>
            ) : (
              <>
                <div className="bg-white text-emerald-500 w-fit mx-auto p-4 rounded-full">
                  <BsTelephoneFill size={30} />
                </div>
                <label htmlFor="phone" className="font-bold text-xl text-white text-center">
                  Verify your phone number
                </label>
                <PhoneInput
                  country={"in"} // Default country (India in this case)
                  value={ph} // Phone number state
                  onChange={setPh} // Update phone number state
                />
                <button
                  onClick={onSignup}
                  className="bg-emerald-600 w-full flex gap-1 items-center justify-center py-2.5 text-white rounded"
                >
                  {loading && (
                    <CgSpinner size={20} className="mt-1 animate-spin" />
                  )}
                  <span>Send code via SMS</span>
                </button>
              </>
            )}
          </div>
        )}
      </div>
    </section>
  );
};

export default App;
